.imgBox {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 20vh;
    min-width: 40vw;
    /* background-color: royalblue; */
}

#logo-img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100px;
    /* background-color: red; */
}

@media (min-width: 600px) {

    .imgBox {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 40vh;
        min-width: 30vw;
    }

    #logo-img {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-height: 320px;
    }
}